import React from "react";
import Typewriter from 'typewriter-effect';

// css imports 
import "./intro.css";

// img imports 
// import Me from "../../images/svg/1.svg"

// Component import 
import Button from "../SmallComponents/Button/Button";

function Intro() {
    return (
        <>


            <section className="section1">

                <div className="container">

                    <div className="row ">

                        <div className="col-lg-12 text-center mtop-5">

                            <h2 className="i-title-in">hello i am</h2>

                            <h1 className="i-name">SNP ZONE</h1>

                            <div className="i-title-iteam mt-3 mb-3">

                                <Typewriter
                                    options={{
                                        strings: ['FRONTEND WEB DEVELOPER', 'RESPONSIVE WEB DESIGN', 'UI/UX DESIGNER', 'PSD TO WEBSITE', 'AND MANY MORE...'],
                                        autoStart: true,
                                        cursor: ' !',
                                        pauseFor: 2000,
                                        loop: true,
                                    }}
                                />

                            </div>

                            <p>
                                i design and develop services for customer's of all sizes, specializing in creating stylish, modern website, online stored, And many more.
                            </p>

                            {/* call Button Component  */}

                            <Button title="KNOW MORE"/>
                       


                        </div>


                     


                    </div>


                </div>


            </section>


        </>


    );
}

export default Intro;
