import React from 'react'

// import css 

import "./Pnavbar.css"

const Pnavbar = ({ filterItem, menuList }) => {
    return (
        <>


            <div className="container">

                <div className="row">

                    <div className="col-lg-12 text-center">


                        <div id="filterNav" key="id" className="filters-list">

                            {menuList.map((curElem) => {
                                
                                return (


                                    <button className="btn mt-2 ml-2 li btn" onClick={() => filterItem(curElem)}>
                                    {curElem}</button>





                                );
                            })}

                        </div>

                    </div>

                </div>

            </div>

        </>
    )
}

export default Pnavbar
