import React, { useState } from 'react'

import Menu from "../Projects/menuApi.json"

import MainCard from '../MainCard/MainCard'
import Pnavbar from '../ProjectNav/Pnavbar.jsx';

// import css file 

import "../../../components/Project/project.css"

const uniqueList = [
    "All",
    ...new Set(
        Menu.map((curElem) => {
            return curElem.category;
        })
    ),
];

// console.log(uniqueList);

const Project = () => {

    const [menuData, setMenuData] = useState(Menu);
    const [menuList] = useState(uniqueList);

    const filterItem = (category) => {
        if (category === "All") {
            setMenuData(Menu);
            return;
        }

        const updatedList = Menu.filter((curElem) => {
            return curElem.category === category;
        });

        setMenuData(updatedList);
    };

    return (
        <>



            <h1 className="text-center">Our Work's</h1>

            <h2 className="text-center m-5">Since Beginning My Journey As A Frontend Developer Nearly Four Years Ago. Here Are A Few Design Projects I've Worked On Recently.</h2>

            <Pnavbar filterItem={filterItem} menuList={menuList}/>

            <MainCard menuData={menuData} />
           
         

        </>
    )
}

export default Project
