import React from 'react'

import "./button.css"

function Button(props) {
    return (
        <>

            <a className="a" href="https://github.com/snpzone" target="_blank" rel="noreferrer">
                <span className="span"></span>
                <span className="span"></span>
                <span className="span"></span>
                <span className="span"></span>
                {props.title}
            </a>

        </>
    )
}

export default Button
