import React from 'react'



const SubCard = ({ menuData }) => {
    return (
        <>

            {/* <h1>I Am SubCard Coming Form SubCard.jsx to MainCard.jsx</h1> */}

            {menuData.map((curElem) => {
                const { id, name, category, image, description, url } = curElem;

                return (

                    <>

                        <div className="col-lg-4 col-md-6 mt-5" key={id}>

                            <div className="card ">
                                <img className="card-img-top img-fluid" src={image} alt="Card img cap" />
                                <div className="card-body ">
                                    <h1 className="card-title ">{name}</h1>
                                    <p className="card-text text-info">{category}.</p>
                                    <p className="card-text ">{description}</p>

                                    <a href={url} className="title" target="_blank" rel="noreferrer"><button type="button" className="btn btn-outline-success btn-rounded" >
                                        Preview
                                    </button></a>


                                </div>
                            </div>




                        </div>

                       
                    </>


                );
            })}
        </>
    )
}

export default SubCard
